import { setTimer } from '@/utils/timer'

export default {
  namespaced: true,
  state: {
    alerts: []
  },

  mutations: {
    ADD_ALERT (state, payload) {
      state.alerts.unshift(payload)
    },
    REMOVE_ALERT (state, payload) {
      state.alerts = state.alerts.filter(el => el.id !== payload)
    },
    REMOVE_ALERTS (state) {
      state.alerts = []
    }
  },

  actions: {
    showAlert ({ commit, state }, payload) {
      if (state.alerts.length > 7) {
        commit('REMOVE_ALERT', state.alerts[state.alerts.length - 1]?.id)
      }
      const timer = setTimer(() => {
        commit('REMOVE_ALERT', payload.id)
      }, payload.time)
      commit('ADD_ALERT', {
        ...payload,
        timer
      })
    }
  },

  getters: {
    getAlerts (state) {
      return state.alerts
    }
  }
}
