import { createStore } from 'vuex'
import { MODULES_NAMES } from '@/common/modules'
import alertModule from '@/store/modules/alert'
import loadingModule from '@/store/modules/loading'

const store = createStore({
  state: {
    isAdmin: false
  },
  mutations: {
    SET_IS_ADMIN (state, payload) {
      state.isAdmin = payload
    }
  },
  actions: {
    setIsAdmin ({ commit }, payload) {
      commit('SET_IS_ADMIN', payload)
    }
  },
  getters: {
    getIsAdmin (state) {
      return state.isAdmin
    }
  },
  modules: {
    [MODULES_NAMES.ALERT]: alertModule,
    [MODULES_NAMES.LOADING]: loadingModule
  }
})

export default store
