export class Timer {
  complete = false
  totalTimeRun = null
  countdown = null
  callback = null
  startTime = null
  ident = null

  constructor (callback, countdown) {
    this.callback = callback
    this.countdown = countdown
    this.startTime = new Date().getTime()
    this.ident = setTimeout(this.callback, this.countdown)
  }

  timeDiff (date1, date2) {
    return date2 ? date2 - date1 : new Date().getTime() - date1
  }

  cancel () {
    clearTimeout(this.ident)
  }

  pause () {
    clearTimeout(this.ident)
    this.totalTimeRun = this.timeDiff(this.startTime)
    this.complete = this.totalTimeRun >= this.countdown
  }

  resume () {
    this.ident = this.complete ? -1 : setTimeout(this.callback, this.countdown - this.totalTimeRun)
  }
}

export const setTimer = (callback, time) => new Timer(callback, time)
