<template>
  <component :is="layout" class="test" />
</template>

<script>
import { computed, defineAsyncComponent } from 'vue'
import { useRoute } from 'vue-router'
import { useViewport } from '@/utils/useViewport'

export default {
  name: 'AppLayout',
  components: {
    DefaultLayout: defineAsyncComponent(() => import('@/layouts/DefaultLayout/DefaultLayout')),
    Admin: defineAsyncComponent(() => import('@/layouts/Admin')),
    Login: defineAsyncComponent(() => import('@/layouts/Login'))
  },
  setup () {
    const route = useRoute()
    useViewport()

    const layout = computed(() => {
      if (route.meta?.layout) {
        return route.meta.layout
      }

      return 'DefaultLayout'
    })

    return { layout }
  }
}
</script>
