
export default {
  namespaced: true,
  state: {
    isLoading: false
  },
  mutations: {
    SET_IS_LOADING (state, payload) {
      state.isLoading = payload
    }
  },
  actions: {
    setIsLoading ({ commit }, payload) {
      commit('SET_IS_LOADING', payload)
    }
  },
  getters: {
    getIsLoading (state) {
      return state.isLoading
    }
  }
}
