import metaData from './meta.json'
import { useRoute } from 'vue-router'
import { useI18n } from 'vue-i18n'
import { watchEffect, computed } from 'vue'

export const useMeta = () => {
  const { locale } = useI18n()
  const route = useRoute()
  const defaultMeta = computed(() =>
    metaData.DEFAULT[locale.value] || metaData.DEFAULT.en
  )
  const meta = computed(
    () => ((metaData[route.name] || metaData.DEFAULT)[locale.value] || defaultMeta.value)
  )

  const changeMeta = (meta = {}) => {
    document.title = meta.title || defaultMeta.value.title
    document
      .querySelector("meta[name='description']")
      .setAttribute('content', meta.description || '')
  }

  watchEffect(() => {
    if (!meta.value) {
      changeMeta()
      return
    }
    changeMeta(meta.value)
  })

  return {
    meta,
    changeMeta
  }
}
